<template>
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 博客列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="newAdd">新增</el-button>
                <!-- <el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">
                    <el-option key="1" label="广东省" value="广东省"></el-option>
                    <el-option key="2" label="湖南省" value="湖南省"></el-option>
                </el-select> -->
                <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="search" @click="search">搜索</el-button>
            </div>
            <el-table :data="blogListData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="title" label="标题" sortable>
                </el-table-column>
                <el-table-column prop="tags" label="标签">
                </el-table-column>
                <el-table-column prop="author" label="作者">
                </el-table-column>
                <el-table-column prop="ctime" label="创建时间">
                </el-table-column>
                <el-table-column prop="views" label="浏览量">
                </el-table-column>
                <el-table-column prop="views" label="状态">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="editor(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="detailBlog(scope.$index, scope.row)">删除</el-button> 
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <!-- <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                </el-pagination> -->

                <el-pagination
                @size-change="sizeChange"
                background
                @current-change="currentChange"
                :current-page="pageIndex"
                :page-sizes="[1, 2, 5, 10]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="totalNum">
                </el-pagination>
            </div>

            


        </div>

        <!-- 编辑弹出框 -->
        <el-dialog :title="visibleTitle" :visible.sync="editVisible" width="80%">
            <el-form ref="form" :model="form" label-width="80px"  >
                <el-form-item label="博客标题" style="width:50%;display: inline-block;">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="博客标签" style="width:50%;display: inline-block;">
                    <el-select v-model="form.tags" multiple placeholder="请选择">
                        <el-option
                        v-for="(item,index) in allTagsData"
                        :key="index"
                        :label="item.tag"
                        :value="item.tag">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="作者" style="width:50%;display: inline-block;">
                    <el-input v-model="form.author"></el-input>
                </el-form-item>
                <el-form-item label="创作时间" style="width:50%;display: inline-block;">
                    <el-date-picker type="date" placeholder="选择日期" v-model="form.date" value-format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <!-- <quill-editor ref="myTextEditor" v-model="form.content" :options="editorOption"></quill-editor> -->
                <div>
                    <el-upload      
                    v-show="false"
                    id="quill-upload"
                    action="http://127.0.0.1:8633/api/web/uploadImg"
                    name="file"
                    multiple
                    :limit="3"
                    list-type="picture"
                    :show-file-list="false"
                    :before-upload="beforeUpload"
                    :on-error="uploadError"
                    :on-success="handleExceed">
                    <el-button size="small" type="primary" ></el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                    <el-row v-loading="uillUpdateImg">
                    <quillEditor
                        ref="myQuillEditor"
                        @change="onEditorChangeq($event)"
                        v-model="form.content"
                        :options="editorOption"/>
                    </el-row>
                </div>
                <!-- <el-button class="editor-btn" type="primary" @click="submit">提交</el-button> -->
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>

import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    import 'quill/dist/quill.bubble.css';
    import { quillEditor } from 'vue-quill-editor';
import axios from '../../axios'
    export default {
        name: 'basetable',
        data() {
            return {
                url: './vuetable.json',
                tableData: [],
                cur_page: 1,
                multipleSelection: [],
                select_cate: '',
                select_word: '',
                del_list: [],
                is_search: false,
                editVisible: false,
                delVisible: false,
                form: {
                    title: '',
                    tags: [],
                    author:'',
                    date:'',
                    content:'',
                },
                idx: -1,

                blogListData:[],
                totalNum:0,
                pageSize:5,
                pageIndex:1,
                content: '',
                allTagsData:[],
                visibleTitle:'',
                blogId:'',//博客id


                
                uillUpdateImg:false,            //根据图片上传状态来确定是否显示loading动画
                serverUrl:'', //上传的图片服务器地址
                value:this.contents,             //富文本内容
                editorOption: {               //符文本编辑器的配置
                    placeholder: '请编写博客',
                    theme: 'snow',
                    modules: {
                    toolbar: {
                    container: [             // 工具栏配置, 默认是全部
                    ['bold'],
                    ['italic'],
                    ['underline'],
                    ['strike'],
                    ['blockquote'], ['code-block'],
                    [{ 'size': ['small', false, 'large', 'huge'] }], 
                    [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
                    [{'list':'ordered'},{'list': 'bullet' }],
                    [{ 'color': [] }, { 'background': [] }], 
                    [{ 'font': [] }],
                    [{ 'align': [] }],
                    [{ 'indent': '-1'}, { 'indent': '+1' }],
                    [{ 'script': 'sub'}, { 'script': 'super' }],
                    [{ 'direction': 'rtl' }],
                    ['link'],
                    ['image'],
                    
                    ],
                    handlers: {
                    'image': function (value) {
                        if (value) {
                        // 给个点击触发Element-ui，input框选择图片文件
                        document.querySelector('#quill-upload input').click()
                        } else {
                        this.quill.format('image', false);
                        }
                    }
                    }
                    }
                    }
                }



            }
        },
        props:['contents'],               //父组件传过来的，将传过来的渲染到富文本编辑器
        components:{
            quillEditor
        },
        created() {
            // this.insertBlog();
            this.pageBlogList();
            this.getData();
        },
        computed: {
            data() {
                return this.tableData.filter((d) => {
                    let is_del = false;
                    for (let i = 0; i < this.del_list.length; i++) {
                        if (d.name === this.del_list[i].name) {
                            is_del = true;
                            break;
                        }
                    }
                    if (!is_del) {
                        if (d.address.indexOf(this.select_cate) > -1 &&
                            (d.name.indexOf(this.select_word) > -1 ||
                                d.address.indexOf(this.select_word) > -1)
                        ) {
                            return d;
                        }
                    }
                })
            }
        },
        methods: {
            // 上传图片
            onEditorChangeq({ quill, html, text }) {   //富文本编辑器内容发生改变的时候
                this.value = html
                this.$emit('textChange',html)       //将富文本编辑器输入的文本发送给父组件，父组件涉及提交添加或者更改
                console.log('quill:',quill,html,text)
            },
            beforeUpload(){               //上传图片之前开启loading
                this.uillUpdateImg = true
            },
            uploadError(){               //图片上传失败,关闭loading
                this.uillUpdateImg = false
                this.$message.error('图片插入失败111111')
                },
            handleExceed(response, file, fileList){   //图片添加成功
                let quill = this.$refs.myQuillEditor.quill
                console.log('pppp:',response,response.status,response.data.url)
                if (response.status == 200) {
                    let length = quill.getSelection().index;
                    // 插入图片 response.data.url为服务器返回的图片地址
                    quill.insertEmbed(length, 'image', response.data.url)
                    // 调整光标到最后
                    quill.setSelection(length + 1)
                }else{
                    this.$message.error('图片插入失败2222222222')
                }
                this.fileList = fileList
                this.uillUpdateImg = false
            },

            // 获取博客数据
            insertBlog(submitArr){
                axios.post('/api/web/insertBlog',submitArr).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功，等待审核。',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.nullBlog();
                        this.queryAllBlog();
                    }
                    console.log('添加返回信息：',res);
                })
                        
            },
            // 分页查询
            pageBlogList(){
                axios.get('/api/web/queryPageBlog',{
                    params:{
                        pageSize:this.pageSize,
                        pageIndex:this.pageIndex,
                    }
                })
                        .then(res=>{
                            this.blogListData=res.data.bloglist;
                            this.totalNum=res.data.total;
                            console.log(this.blogListData,res);
                        })
            },
            queryAllBlog(){
                axios.get('/api/web/queryAllBlog')
                        .then(res=>{
                            this.blogListData=res.data;
                            console.log(this.blogListData);
                        })
            },
            detailBlog(index, row) {
                // this.idx = index;
                // this.delVisible = true;
                
                console.log('显示信息:',index,row);
                // alert('显示信息:',index,row)
                console.log(row._id)
                axios.post('/api/web/dellBlog',{
                    id:row._id
                }).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，删除成功！',
                            type: 'success'
                        });
                        this.queryAllBlog();
                    }
                    console.log(res);
                })
            },
            // 获取所有标签
            getAllTags(){
                axios.get('/api/web/queryAllTags')
                    .then(res=>{
                        console.log(res)
                        this.allTagsData=res.data;
                    }).catch(err=>{
                        console.log(err)
                    })
            },
            //新增博客
            newAdd(){
                this.nullBlog();
                this.getAllTags();
                this.visibleTitle='新增博客';
                this.editVisible = true;
                // this.insertBlog();
            },
            // 清除form内容
            nullBlog(){
                this.form={
                    title: '',
                    tags: '',
                    author:'',
                    date:'',
                    content:'',
                }
            },

            onEditorChange({ editor, html, text }) {
                this.content = html;
            },
            submit(){
                console.log(this.content);
                this.$message.success('提交成功！');
            },

            // 保存编辑
            saveEdit() {
                // this.$set(this.tableData, this.idx, this.form);
                // alert(this.form)
                if(this.visibleTitle=='编辑博客'){
                    let subArr = {
                        id:this.blogId,
                        title:this.form.title,
                        content:this.form.content,
                        author:this.form.author,
                        views:8,
                        tags:this.form.tags.toString(),
                        ctime:this.form.date || new Date(),
                        uptime:this.form.date || new Date()
                    }
                    console.log('主体信息',subArr);
                    axios.post('/api/web/updateOneBlog',subArr).then(res=>{
                        if(res.status==200){
                            this.$message({
                                showClose: true,
                                message: '恭喜你，添加成功，等待审核。',
                                type: 'success'
                            });
                            this.editVisible = false;
                            this.nullBlog();
                            this.queryAllBlog();
                        }
                        console.log('添加返回信息：',res);
                    })

                }else if(this.visibleTitle=='新增博客'){
                    let subArr = {
                        title:this.form.title,
                        content:this.form.content,
                        author:this.form.author,
                        views:8,
                        tags:this.form.tags.toString(),
                        ctime:this.form.date || new Date(),
                        uptime:this.form.date || new Date()
                    }
                    this.insertBlog(subArr);
                    console.log('提交信息：',this.form)
                }
                
            },

            // 编辑博客
            editor(index,row){
                this.nullBlog();
                this.getAllTags();
                this.visibleTitle='编辑博客';
                this.editVisible = true;
                console.log('这是id:',row._id)
                axios.get('/api/web/queryOneBlog',{
                    params:{
                        id:row._id
                    }
                })
                .then(res=>{
                    console.log('编辑的数据：',res)
                    if(res.status==200){
                        this.form={
                            title:res.data[0].title,
                            tags: res.data[0].tags.split(','),
                            // tags: [],
                            author:res.data[0].author,
                            date:res.data[0].ctime,
                            content:res.data[0].content,
                        }
                        this.blogId=row._id;
                    }
                })
            },

            // 批量删除
            delAll() {
                let str = [];
                this.multipleSelection.forEach((item,index) => {
                    str.push(item._id)
                });
                console.log(str)
                axios.post('/api/web/dellBatchBlog',{
                    idArr:str
                }).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，批量删除成功！',
                            type: 'success'
                        });
                        this.queryAllBlog();
                    }
                    console.log(res);
                })
                // this.multipleSelection = [];
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection);
            },
            
            currentChange(val){
                this.pageIndex=val;
                console.log(`当前页: ${val}`);
                this.pageBlogList();
            },
            sizeChange(val){
                this.pageSize=val;
                console.log(`每页 ${val} 条`);
                this.pageBlogList();
            },





            // 分页导航
            handleCurrentChange(val) {
                this.cur_page = val;
                this.getData();
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
                if (process.env.NODE_ENV === 'development') {
                    this.url = '/ms/table/list';
                };
                // this.$axios.post(this.url, {
                //     page: this.cur_page
                // }).then((res) => {
                //     this.tableData = res.data.list;
                // })
            },
            search() {
                this.is_search = true;
            },
            formatter(row, column) {
                return row.address;
            },
            filterTag(value, row) {
                return row.tag === value;
            },
            handleEdit(index, row) {
                this.idx = index;
                const item = this.tableData[index];
                this.form = {
                    name: item.name,
                    date: item.date,
                    address: item.address
                }
                this.editVisible = true;
            },
            
            
            
            // 确定删除
            deleteRow(){
                this.tableData.splice(this.idx, 1);
                this.$message.success('删除成功');
                this.delVisible = false;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
    .mr10{
        margin-right: 10px;
    }
</style>
